
import { defineComponent } from "vue";
import {
  IonItem,
  IonLabel,
  IonList,
  IonGrid,
  IonRow,
  IonCol,
  IonItemSliding,
  IonItemOptions,
  IonItemOption,
  IonIcon,
  alertController
} from "@ionic/vue";
import { Vocable } from "@/data/vocable";
import { trash, pencil } from "ionicons/icons";
import { getTranslation } from "@/main";

export default defineComponent({
  name: "VocableList",
  components: {
    IonItem,
    IonLabel,
    IonList,
    IonGrid,
    IonRow,
    IonCol,
    IonItemSliding,
    IonItemOptions,
    IonItemOption,
    IonIcon
  },
  props: {
    vocables: {
      type: Array as () => Vocable[],
      required: true,
    },
  },
  setup() {
    return {
      trash,
      pencil,
    };
  },
  methods: {
    async deleteVocable(id: number) {
      const alert = await alertController.create({
        header: getTranslation().t("Do you really want to delete this vocable?"),
        buttons: [
          {
            text: getTranslation().t("Cancle"),
            role: "cancel",
          },
          {
            text: getTranslation().t("DELETE"),
            role: "confirm",
            handler: () => { 
              this.$emit("deleteVocable", id);
            },
          },
        ],
      });

      await alert.present();
    },
    editVocable(id: number) {
      this.$emit("editVocable", id);
    },
  },
});
